import { Alignment, AlignmentType, Document, Packer, Paragraph, TextRun, UnderlineType } from "docx";
import { saveAs } from "file-saver";
import { parseText, ScreenPlayElements, Direction, Dialogue } from "./parser";

const parseUserInput = (): ScreenPlayElements[] => {
  const userInput = document.getElementById("userInput") as HTMLTextAreaElement;
  console.log(userInput);
  // Parse text
  if (userInput.value) {
    console.log(userInput.value);
    return parseText(userInput.value);
  } else {
    return [];
  }
};

const liveRendering = (text: string) => {
  const testElement = document.getElementById("test");

  if (testElement == null) {
    return;
  }

  // Create a new paragraph element with text content
  testElement.innerHTML = "";

  // Parse text from userInput
  const elements = parseUserInput();

  // Append the paragraph to the div
  for (let i = 0; i < elements?.length; i++) {
    const element = elements[i];
    testElement.appendChild(element.getRenderedHtml());
  }
};

////////

///// Plug live rendering on user input

// Get the input element by its ID
const inputElement = document.getElementById(
  "userInput"
) as HTMLTextAreaElement;

// Add an event listener to capture the input change event
inputElement.addEventListener("input", (event) => {
  const target = event.target as HTMLInputElement;
  const text = target.value;
  liveRendering(text);
});

///

async function generateDoc() {
  // Get the text from the input field
  const section_children = [];
  const elements = parseUserInput();

  for (let i = 0; i < elements.length; i++) {
    section_children.push(...elements[i].getRenderedDocxParagraph());
  }

  // The first argument is an ID you use to apply the style to paragraphs
  // The second argument is a human-friendly name to show in the UI
  let doc = new Document({
    creator: "Clippy",
    title: "Sample Document",
    description: "A brief example of using docx",
    sections: [{
      children: section_children
    }],
    styles: {
      paragraphStyles: [
        {
          id: "Heading 2",
          name: "Heading 2",
          basedOn: "Normal",
          next: "Normal",
          quickFormat: true,
          run: {
            bold: true,
            size: 26, 
            font: "Times New Roman"
          },
          paragraph: {
            alignment: AlignmentType.CENTER
          },
        },
        {
          id: "Normal",
          name: "Normal",
          basedOn: "Normal",
          next: "Normal",
          quickFormat: true,
          run: {
            bold: false,
            size: 26, 
            font: "Times New Roman"
          },
          paragraph: {
          },
        },
      ],
    },
  });

  // Pack the document into a blob and trigger download
  const blob = await Packer.toBlob(doc);
  saveAs(blob, "generated_document.docx");
}

///// Plug download button

const downloadButtonCallback = () => {
  generateDoc();
};

const downloadButton = document.getElementById(
  "downloadButton"
) as HTMLButtonElement;
downloadButton.addEventListener("click", downloadButtonCallback);

///// Plug save button

const saveButtonCallback = () => {
  console.log("Save")
  saveUserInputIntoCookie()
};

const saveButton = document.getElementById(
  "saveButton"
) as HTMLButtonElement;

saveButton.addEventListener("click", saveButtonCallback);

///// Restore cookie
/*
 * General utils for managing cookies in Typescript.
 */
export function setCookie(name: string, val: string) {
  const date = new Date();
  const value = encodeURIComponent(val)

  // Set it expire in 7 days
  date.setTime(date.getTime() + (7 * 24 * 60 * 60 * 1000));

  // Set it
  document.cookie = name+"="+value+"; expires="+date.toUTCString()+"; path=/";
}

export function getCookie(name: string): string {
  const value = "; " + document.cookie;
  const parts = value.split("; " + name + "=");
  
  if (parts.length == 2) {
      return decodeURIComponent(parts.pop().split(";").shift());
  }
  return ""
}

export function deleteCookie(name: string) {
  const date = new Date();

  // Set it expire in -1 days
  date.setTime(date.getTime() + (-1 * 24 * 60 * 60 * 1000));

  // Set it
  document.cookie = name+"=; expires="+date.toUTCString()+"; path=/";
}

function saveUserInputIntoCookie() {
  const userInput = document.getElementById("userInput") as HTMLTextAreaElement;
  if (userInput.value) {
    deleteCookie("userInput")
    setCookie("userInput", userInput.value)
  }
}

function restoreUserInputFromCookie() {
  let userInput = document.getElementById("userInput") as HTMLTextAreaElement;
  userInput.value = getCookie("userInput")
}

restoreUserInputFromCookie()

